<template>
  <div class="sign-container">
    <el-card :body-style="{ padding: '20px' }">
      <div slot="header" class="clearfix">
        <span>3</span>
      </div>
      <el-form>
        <el-form-item label=""></el-form-item>
        <el-form-item v-for="(item,index) in list" :key="index" label=''>
         {{ item.snName }} 
          <el-input-number v-model="item.points" :precision="0" :min="0" controls-position="right"></el-input-number>
          
          <span class="ml20">
            
          </span>
        </el-form-item>
      </el-form>
    </el-card>
    <div style="height: 30px"></div>
    <el-card :body-style="{ padding: '20px' }">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <el-form>
        <el-form-item label=""></el-form-item>
        <el-form-item label="5">
          
          <el-input-number v-model="form.contDay5" :precision="0" :min="0" controls-position="right"></el-input-number>
          
        </el-form-item>
        <el-form-item label="6">
          
          <el-input-number v-model="form.contDay6" :precision="0" :min="0" controls-position="right"></el-input-number>
          
        </el-form-item>
        <el-form-item label="7">
          
          <el-input-number v-model="form.contDay7" :precision="0" :min="0" controls-position="right"></el-input-number>
          
        </el-form-item>
      </el-form>
    </el-card>
    <div style="height: 40px;text-align: center;margin-top: 30px">
      <el-button type="primary" @click="handleSave"></el-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "SignMgr",
    data(){
      return {
        list:[
          {
            sn:'1234567',
            snName:'',
            points:0
          },
          {
            sn:'6901028250009',
            snName:'',
            points:0
          },
          {
            sn:'6901028250382',
            snName:'',
            points:0
          },
          {
            sn:'6901028250234',
            snName:'',
            points:0
          },
          {
            sn:'6901028250351',
            snName:'',
            points:0
          },
          {
            sn:'1111111111111',
            snName:'',
            points:0
          },
          {
            sn:'1111111111112',
            snName:'',
            points:0
          },
          {
            sn:'1111111111114',
            snName:'',
            points:0
          },
          {
            sn:'6901028078245',
            snName:'',
            points:0
          },
          {
            sn:'6901028079075',
            snName:'',
            points:0
          },
          {
            sn:'6901028250788',
            snName:'',
            points:0
          },
          {
            sn:'1111111111115',
            snName:'',
            points:0
          },
          {
            sn:'6901028077699',
            snName:'',
            points:0
          },
          {
            sn:'6901028080019',
            snName:'',
            points:0
          },
          {
            sn:'6901028250757',
            snName:'2',
            points:0
          },
          {
            sn:'6901028079815',
            snName:'',
            points:0
          },
          {
            sn:'6901028250320',
            snName:'',
            points:0
          },
          {
            sn:'6901028250412',
            snName:'',
            points:0
          },
          {
            sn:'6901028250979',
            snName:'',
            points:0
          },
          {
            sn:'6901028250597',
            snName:'',
            points:0
          },
          {
            sn:'6901028107105',
            snName:'',
            points:0
          },
          {
            sn:'6901028250955',
            snName:'',
            points:0
          },
          {
            sn:'6901028107198',
            snName:'',
            points:0
          },
          {
            sn:'6901028250790',
            snName:'',
            points:0
          },
          {
            sn:'6901028077460',
            snName:'()',
            points:0
          },

        ],
        form:{
          num:0,
          contDay5:0,
          contDay6:0,
          contDay7:0,
        }
      }
    },
    created() {
      this.getScanConf()
      this.getContConf()
    },
    mounted() {
    },
    methods:{
      // 
      getScanConf(){
        this.$request.post('/hbact/dayscan/conf/list', {}, true, res => {
          if (res.code == '200') {
              this.list.forEach((e)=>{
                let data = res.data
                data.forEach((i)=>{
                  if(e.sn == i.sn){
                    e.points = i.points
                  }
                })
              })
            // console.log(res.data)
            return
          }
          this.$message.error(res.msg)
        })
      },
      // 
      getContConf(){
        this.$request.post('/hbact/dayscan/cont/list', {}, true, res => {
          if (res.code == '200') {
            let data = res.data
            data.forEach((e,i)=>{
              if(e.contDay == 5){
                this.form.contDay5 = e.points
              }
              else if(e.contDay == 6) {
                this.form.contDay6 = e.points
              }
              else if(e.contDay == 7) {
                this.form.contDay7 = e.points
              }
            })
            return
          }
          this.$message.error(res.msg)
        })
      },
      //
      dayscanUpdate(){
        return new Promise ((resolve,reject)=>{
          let params = this.list.map((e,i)=>{
            return {
              "sn": e.sn,
              "snName": e.snName,
              "points": e.points
            }
          })
          this.$request.post('/hbact/dayscan/conf/update', params, true, res => {
            resolve(res)
          })
        })
      },
      //
      contDayUpdate(){
        return new Promise ((resolve,reject)=>{
          this.$request.post('/hbact/dayscan/cont/update', [
            {
              "contDay":5,
              "points": this.form.contDay5
            },
            {
              "contDay":6,
              "points": this.form.contDay6
            },
            {
              "contDay":7,
              "points": this.form.contDay7
            }
          ], true, res => {
            resolve(res)
          })
        })
      },
      handleSave(){
        Promise.all([
          this.dayscanUpdate(),
          this.contDayUpdate(),

        ])
        .then(([dayscan, contDay]) => {
          if (dayscan.code == '200') {
            console.log(dayscan.data)
          }else {
            this.$message.error(dayscan.msg)
            return
          }
          if (contDay.code == '200') {
            console.log(contDay.data)
          }else {
            this.$message.error(contDay.msg)
            return
          }
          this.$message.success('')
        })
      }
    }

  }
</script>

<style lang="scss" scoped>
  .sign-container{
    background: #fff;
    padding: 30px;
    box-sizing: border-box;

  }
</style>


