<template>
  <div class="QA-container">
    <div >
      <el-card :body-style="{ padding: '20px' }">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <div style="margin-bottom: 20px">:<el-button size="" style="margin-left: 20px"  @click="getList(3)"></el-button></div>
        <el-form>
          <el-form-item v-for="(item,index) in hb" :key="index" label=''>
             <el-input-number v-model="item.redMoney" :disabled="item.id ? true : false" :precision="2" :min="0" controls-position="right"></el-input-number>
            <span style="margin-right: 20px"></span>
             <el-input-number v-model="item.totalNum" :disabled="item.id ? true : false" :precision="0" :min="0" controls-position="right"></el-input-number>

            <span v-if="item.id ? true : false">
               <span style="margin-right: 20px"></span>
            {{ item.totalNum - item.outNum }}
            </span>
            <span style="margin-right: 20px"></span>
            :{{ item.redMoney*item.totalNum }}
            <span style="margin-right: 20px"></span>
             <el-input-number v-model="item.probability" :precision="0" :min="0" controls-position="right"></el-input-number>
            %
            <span v-if="item.id ? true : false">
              <span style="margin-right: 20px"></span>
              <el-button type='primary' @click="addRepertory(item)"></el-button>
            </span>
            <span style="margin-right: 20px"></span>
            <el-button type='danger' @click="deleteAward('hb',index)"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <el-card :body-style="{ padding: '20px' }">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <div style="margin-bottom: 20px">:<el-button style="margin-left: 20px"  @click="getList(6)"></el-button></div>
        <el-form>
          <el-form-item v-for="(item,index) in hsb" :key="index" label=''>
             <el-input-number v-model="item.integral" :disabled="item.id ? true : false" :precision="0" :min="0" controls-position="right"></el-input-number>
            <span style="margin-right: 20px"></span>
            <el-input-number v-model="item.totalNum" :disabled="item.id ? true : false" :precision="0" :min="0" controls-position="right"></el-input-number>
            <span v-if="item.id ? true : false">
               <span style="margin-right: 20px"></span>
            {{ item.totalNum - item.outNum }}
            </span>
            <span style="margin-right: 20px"></span>
            :{{ item.integral*item.totalNum }}
            <span style="margin-right: 20px"></span>
            <el-input-number v-model="item.probability" :precision="0" :min="0" controls-position="right"></el-input-number>
            %
            <span v-if="item.id ? true : false">
              <span style="margin-right: 20px"></span>
              <el-button type="primary" @click="addRepertory(item)"></el-button>
            </span>
            <span style="margin-right: 20px"></span>
            <el-button type='danger' @click="deleteAward('hsb',index)"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <div style="height: 30px"></div>
    </div>


    <div style="height: 40px;text-align: center;margin-top: 30px">
      <el-button type="primary" @click="confirmSubmit"></el-button>
    </div>

    <!--  -->
    <el-dialog :title="title" :visible.sync="listVisible" width="800px">
      <el-table :data="list" border :stripe="true" style="width: 100%">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <img :src="scope.row.pic" alt="" style="height: 60px">
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="selectPrize(scope.row,title)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24">
        <el-pagination background @size-change="handSizeChange" @current-change="handCurrentChange" :current-page="params.pageNo" :page-size="params.pageSize" layout="total, prev, pager, next, jumper" :total="listTotal">
        </el-pagination>
      </el-col>
      <div style="clear:both"></div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "QAMgr",
    data(){
      return {
        id:'',
        actCode:'',
        strategyArr:{},
        act:{},


        title: '',
        list: [],
        params: {
          metraFlag: '',
          pageNo: 1,
          pageSize: 10,
          status: 1
        },
        listTotal: 0,
        listVisible: false,

        hb:[],
        hsb:[],
        defaultAwae: { //  
          awardPic: '',
          awardType: 1, // 
          curActive: true,
          giveScore: 0, //  0- 1-
          guideGzh: 0, //  0- 1-
          hasPdMaxOut: false,
          hasWarn: false,
          integral: null, //  
          integralPool: null, // id
          integralPoolName: null,
          integralPoolPic: null,
          isGiveScore: false,
          isGuideGzh: false,
          isPdMaxOut: false,
          isWarn: false,
          n: '',
          outNum: '',
          pdMaxOut: '', // 
          poolId: 1, // id
          poolName: '',
          prizeName: '', // 
          probability: '', // 
          redMoney: '', // 
          redTotalMoney: '',
          remainNum: 0,
          totalNum: '', // totalNum
          warnValue: '' // 0
        },
      }
    },
    created() {
      this.getActCode()
    },
    mounted() {
    },
    computed: {
      residue: {
        get: function() {
          return this.awae.totalNum - this.awae.outNum
        },
        set: function(newValue) {
          // console.log(newValue)
        }
      },
      totalRed: {
        get: function() {
          return this.awae.redMoney * this.awae.totalNum
        },
        set: function(newValue) {
          // console.log(newValue)
        }
      }
    },
    methods:{
      // 
      getActCode(){
        this.$request.post('/hbact/hyr/home/actCode', {actType:6}, false, res => {
          if (res.code == '200') {
            this.actCode = res.data.actCode
            this.id = res.data.id
            this.getDetail()
            return
          }
          this.$message.error(res.msg)
        })
      },
      getDetail() {
        this.$request.post('/api/wiseqr/act/detail', { id: this.id }, true, res => {
          if (res.ret == '200000') {

            this.strategyArr = res.data.strategyArr[0];
            this.act = res.data.act
            let awardArr = this.strategyArr.awardArr
            this.hb = []
            this.hsb = []
            awardArr.forEach((e,i)=>{
              if(e.awardType == 3){
                this.hb.push(e)
              }
              else if(e.awardType == 6){
                this.hsb.push(e)
              }
            })
            return
          }
          this.$message.error(res.messgae)
        })
      },
      getList(type) {
        if (type == '1') {
          this.params.metraFlag = 'object'
          this.title = ''
        } else if (type == '2') {
          this.params.metraFlag = 'virtual'
          this.title = ''
        } else if (type == '3') {
          this.params.metraFlag = 'redpack'
          this.title = ''
        } else if (type == '6') {
          this.params.metraFlag = 'integral'
          this.title = ''
        }
        this.$request.post('/api/wiseqr/metra/list', this.params, true, res => {
          if (res.ret === '200000') {
            this.list = []
            this.list = res.data.list
            this.listTotal = res.data.page.count
            this.listVisible = true
            return
          }
          this.$message.error(res.message)
        })
      },
      addRepertory(item) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d{1,}$/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          if (value == 0) return this.$message.error('0')
          this.$request.post('/api/wiseqr/act/addNum', {
            id: item.id ,
            increment: value
          }, true, res => {
            if (res.ret === '200000') {
              this.$message.success('')
              this.getDetail()
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      confirmSubmit() {
        let params = {};
        // params.act = {
        //   actCode: this.act.actCode,
        //   actDesc: this.act.actDesc,
        //   actName: this.act.actName,
        //   banner: this.act.banner,
        //   etimeStr: this.act.etimeStr,
        //   extInfo: this.act.extInfo,
        //   form: this.act.form,
        //   id: this.act.id,
        //   idx: this.act.idx,
        //   note: this.act.note,
        //   selectBrand: this.strategyArr.brandArr,
        //   selectCityList: this.strategyArr.areas.cityArr,
        //   selectDistrictList: this.strategyArr.areas.districtArr,
        //   selectProductList: this.strategyArr.snArr,
        //   selectProvList: this.strategyArr.areas.provinceArr,
        //   showStatus: this.act.showStatus,
        //   status: this.act.status,
        //   stimeStr: this.act.stimeStr,
        //   tplCode: this.act.tplCode,
        // }
        params.act = {
          actCode: this.act.actCode,
          dwnum: this.act.dwnum,
          id: this.act.id,
          status: this.act.status,
        }
        params.strategyArr = [
          {
            areas:{
              provinceArr:this.strategyArr.areas.provinceArr,
              cityArr:this.strategyArr.areas.cityArr,
              districtArr:this.strategyArr.areas.districtArr,
            },
            awardArr:[...this.hb,...this.hsb],
            snArr:this.strategyArr.snArr,
            brandArr:this.strategyArr.brandArr,
            tf:{
              id:this.strategyArr.tf.id,
              tfCode:this.strategyArr.tf.tfCode,
            },
            tfType: this.strategyArr.tfType,
          }
        ]
        this.$request.post('/api/wiseqr/act/somtf', params, true, res => {
          if (res.ret == '200000') {
            this.getDetail()
            this.$message({type: 'success', message: '!'});

          } else {
            this.$message.error(res.message);
          }
        })

      },
      // 
      selectPrize(obj,title) {
        let newAwae = JSON.parse(JSON.stringify(this.defaultAwae))
        newAwae.awardPic = obj.pic
        newAwae.poolName = obj.name
        newAwae.prizeName = obj.name
        newAwae.poolId = obj.id
        if(title == ''){
          newAwae.awardType = 3
          this.hb.push(newAwae)
        }
        if(title == ''){
          newAwae.awardType = 6
          this.hsb.push(newAwae)
        }
        this.listVisible = false
      },
      deleteAward(list,index){
        this[list].splice(index,1)
      },
      handSizeChange(newSize) {
        this.params.pageSize = newSize
        this.getList()
      },
      handCurrentChange(newSize) {
        this.params.pageNo = newSize
        this.getList()
      },
    }

  }
</script>

<style lang="scss" scoped>
  .QA-container{
    background: #fff;
    padding: 30px;
    box-sizing: border-box;
  }
</style>


